<template>
  <div>
    <top></top>
    <div class="infom_top">
      <p style="padding: 0.2rem 0;display: flex;align-items: center">
        <Icon type="chatbubbles" color="#25B3EF" size="20"></Icon>
        <span style="margin-left: 0.1rem;font-size: 0.14rem">微信号提醒 : </span>
      </p>
      <span style="display: inline-block;margin-left: 0.26rem;">请绑定 <a href="javascript:void(0)">「西瓜科技」官方微信号</a></span>
      <p style="width: 98%;border-bottom: 0.01rem solid #DFDFDF;margin-top: 0.1rem;"></p>
      <p style="padding: 0.2rem 0;display: flex;align-items: center">
        <Icon type="iphone" color="#25B3EF" size="20"></Icon>
        <span style="margin-left: 0.1rem;font-size: 0.14rem">手机短信提醒 : </span>
      </p>
      <div style="display: flex">
        <div>
          <input type="number" class="input" placeholder="请输入手机号" @focus="textF" @blur="change" v-model="tel" style="border-bottom-right-radius: 0;border-top-right-radius:0;">
          <a href="javascript:void(0)" class="text_a" @click="getMessage" v-if="!show">{{msg}}</a>
          <a href="javascript:void(0)" class="text_a" v-if="show">{{time}}s重新获取</a><br>
          <span style="margin-top:0.1rem;color: #e64751;display: inline-block">{{telMsg}}</span>
        </div>
        <div style="margin-left: 0.1rem">
          <input type="text" class="input"  style="width: 1.8rem;" placeholder="请输入验证码" @focus="textF" @blur="input(1)" v-model="message">
          <span style="margin-top:0.1rem;color:#e64751;display: inline-block">{{messageMsg}}</span>
        </div>
      </div>
      <p style="width: 98%;border-bottom: 0.01rem solid #DFDFDF;margin-top: 0.1rem;"></p>
      <p style="padding: 0.2rem 0;display: flex;align-items: center">
        <Icon type="email" color="#25B3EF" size="20"></Icon>
        <span style="margin-left: 0.1rem;font-size: 0.14rem">邮件提醒 : </span>
      </p>
      <p style="padding-bottom: 0.1rem">
        <input type="text" class="input" placeholder="请输入邮箱地址" @focus="textF" @blur="emailed" v-model="email">
        <p style="color:#e64751;margin-bottom: 0.1rem">{{emailMsg}}</p>
      </p>
      <a href="javascript:void(0)" class="a" @click="save" v-if="!load">保存</a>
      <a href="javascript:void(0)" class="a" v-if="load">保存 <img src="../../assets/bai.gif" alt="" style="width: 20px;max-height: 100%"></a>
    </div>

  </div>
</template>
<script>
  import top from '../top.vue'
  export default {
    data() {
      return {
        load: false,
        show: false,
        tel: '',
        email: '',
        telMsg: '',
        emailMsg: '',
        message: '',
        messageMsg: '',
        msg: '获取验证码',
        time: 60
      }
    },
    methods: {
      save() {
        this.load = true;
        this.post('custom/Own/setNotifyInfo', {
          notifytel: this.tel,
          notifyemail: this.email
        }).then(res => {
          this.load = false;
          if(res.data.code === 10000) {
            const content = "信息设置成功";
            this.$Modal.success({
              title: "设置成功",
              content: content
            });
          } else {
            const content = res.data.msg_test + "，请重新设置";
            this.$Modal.error({
              title: "设置失败",
              content: content
            });
          }
        })
      },
      getMessage() {
        if(this.tel !== "") {
          this.show = true;
          const timer = setInterval(() => {
            this.time--;
            if(this.time === 0) {
              this.msg = "重新获取验证码";
              this.show = false;
              this.time = 60;
              clearInterval(timer);
            }
          }, 1000);
          this.post('custom/Own/sendMsg', {
            tel: this.tel,
          }).then(res => {
            if(res.data.code === 10000) {
              this.$Modal.success({
                title: "发送成功",
                content: "您的手机号验证码已发送成功请注意查收"
              });
            } else {
              this.$Modal.error({
                title: "发送失败",
                content: "手机号发送失败，请重新发送"
              });
            }
          })
        } else {
          this.$Modal.error({
            title: "获取失败",
            content: "请输入手机号"
          });
        }
      },
      textF() {
        event.target.style.borderColor = 'skyblue';
      },
      input(val) {
        event.target.style.borderColor = 'gainsboro';
        if(val === 1) {
          this.post('custom/Own/verifyMsgCode', {
            tel: this.tel,
            code: this.message
          }).then(res => {
            if(res.data.code === 10000) {
              this.messageMsg = res.data.msg_test
            } else if(res.data.code === 10003) {
              this.messageMsg = res.data.msg_test;
            }
          })
        }
      },
      change() {
        let myreg = /^(((13[0-9]{1})|(15[0-9]{1})|(18[0-9]{1}))+\d{8})$/;
        if(this.tel && myreg.test(this.tel)) {
          this.telMsg = '';
          event.target.style.borderColor = 'gainsboro';
        } else {
          this.telMsg = '手机号格式不正确';
          event.target.style.borderColor = '#e64751';
        }
      },
      emailed() {
        let RegEmail = /\w[-\w.+]*@([A-Za-z0-9][-A-Za-z0-9]+\.)+[A-Za-z]{2,14}/;
        if(this.email && RegEmail.test(this.email)) {
          this.emailMsg = '';
          event.target.style.borderColor = 'gainsboro';
        } else {
          this.emailMsg = '邮箱格式不正确';
          event.target.style.borderColor = '#e64751';
        }
      },
    },
    mounted() {
      this.post('custom/Own/getNotifyInfo', {}).then(res => {
        if(res.data.code === 10000) {
          this.tel = res.data.data.notifytel;
          this.email = res.data.data.notifyemail;
        }
      })
    },
    components: {
      top
    }
  }
</script>
<style scoped="scoped">
  .input {
    display: inline-block;
    width: 2.5rem;
    border: 1px solid #E5E5E5;
    padding: 0.1rem 0.1rem;
    border-radius: 5px;
  }
  
  .a {
    color: white;
    display: inline-block;
    padding: 0.09rem 0.3rem;
    border-radius: 0.03rem;
    background-color: #59A2ED;
  }
  .infom_top{
    margin-top: 0.2rem;
    background-color: white;
    padding-left: 0.2rem;
    padding-bottom: 0.2rem;
    box-shadow: 5px 5px 10px #E7E7E7;
  }
  .text_a{
    display: inline-block;
    padding: 0.11rem 0.1rem;
    background-color: #59A2ED;
    color: white;
    margin-left: -0.03rem;
    
  }
</style>


