<template>
  <div class="top">
    <div>
      <i class="iconfont icon-tishi"></i>
      <!--<i :class="className" style="font-size: 0.2rem;margin-right: 0.1rem"></i>-->
      <span class="hint">{{msg}}</span>
      
    </div>
      
  </div>
</template>
<script>
  export default{
    data(){
      return{
        className:'',
        name:'',
        msg:''
      }
    },
    mounted(){
      let name=this.$route.name;
      name = name.indexOf('hotel')!==-1?name.substring(5).toLowerCase():name
      switch(name){
        case 'goods':
            this.className='iconfont icon-pricetag';
            this.name='商品';
            this.msg='小程序发布后，更新商品将实时同步，无需再次发布。';
            break;
        case 'info':
          this.className='iconfont icon-xinxi';
          this.name='基本信息';
           this.msg='设置小程序的基本信息，如名称等等';
          break;
        case 'pay':
          this.className='iconfont icon-icon-wx-pay';
          this.name='微信支付';
          this.msg='微信小程序仅支持微信支付（商户号），且已完成微信认证。个人用户暂不能开通支付功能。';
          break;
        case 'style':
          this.className='iconfont icon-style-edit';
          this.name='风格';
          this.msg='设置小程序显示的风格，如头部颜色，轮播图等等 ';
          break;
        case 'card':
          this.className='iconfont icon-vip-';
          this.name='优惠券';
          this.msg=' 设置优惠信息，如满减，买多少免邮费等等';
          break;
        case 'sorts':
          this.className='iconfont icon-quanbushangpinfenlei';
          this.name='商品分类';
          this.msg='添加新的商品分类，为商品分类排序，删除商品分类。你可以在编辑商品时添加商品分类。';
          break;
        case 'delivery':
          this.className='iconfont icon-peisong';
          this.name='配送';
          this.msg='设置配送信息，如默认哪家快递等等';
          break;
        case 'inform':
          this.className='iconfont icon-bell-hl';
          this.name='通知提醒';
          this.msg='这是用来跟客户沟通的联系方式，有新订单生成或者库存短缺时，我们也会发送通知。';
          break;
        case 'bookInform':
          this.className='iconfont icon-bell-hl';
          this.name='通知提醒';
          this.msg='这是用来跟客户沟通的联系方式，有新订单生成或者库存短缺时，我们也会发送通知。';
          break;
        case 'bookStyle':
          this.className='iconfont icon-tishi';
          this.name='风格';
          this.msg='设置小程序显示的风格，如头部颜色，轮播图等等';
          break;
      }
    }
  }
</script>
<style scoped="scoped">
  @import url("../../static/base.css");
</style>



